@-webkit-keyframes fadeIn{
    0%{
        visibility: hidden;
        margin-top: 30px;
        opacity: 0;
    }
    50%{
        visibility: visible;
        margin-top: 15px;
        opacity: 0.3;
    }
    100%{
        margin-top: 0;
        opacity: 1;
    }
}

@keyframes fadeIn{
    0%{
        visibility: hidden;
        margin-top: 30px;
        opacity: 0;
    }
    50%{
        visibility: visible;
        margin-top: 15px;
        opacity: 0.3;
    }
    100%{
        margin-top: 0;
        opacity: 1;
    }
}

@-webkit-keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }

@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }

  .rotate {
    -webkit-animation: rotation 0.5s;
            animation: rotation 0.5s;
  }

.textoInicio{
    -webkit-animation: fadeIn ease-in 0.5s;
            animation: fadeIn ease-in 0.5s;
}

*{
    margin: 0;
    padding: 0;
}

.contenedor{
    width: 100%;
    font-family: 'Ubuntu', sans-serif;
    background-color: rgb(245, 245, 245);
    overflow: hidden;
}

.contactos{
    background-color: rgb(119, 119, 119);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 150px;
}

.contactos div{
    display: flex;
    flex-direction: row;
    margin: auto 0;
    color: #fff;
}

.contactos div p{
    padding: 0 5px;
    padding-right: 10px;
}

.contactos div p a{
    text-decoration: none;
    color: #fff;
    padding: 0;
}

.redes{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.redes li{
    padding: 0 10px;
    font-size: 20px;
    text-decoration: none;
}

.redes li a{
    color: #fff;
}

.redes li a:hover{
    color: rgb(233, 233, 233);
}

.encabezado{
    position: fixed;
    width: 100%;
    height: 80px;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 100px;
    z-index: 5;
}

.encabezado .logo{
    max-height: 75px;
    max-width: 75px;
    margin: auto 0;
}

.encabezado .logo .logo-img{
    width: 100%;
    height: 100%;
}

.encabezado .boton-menu{
    visibility: hidden;
    cursor: pointer;
}

#titulo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: rgb(55, 110, 182);
}

.encabezado .menu{
    visibility: visible;
    padding: 0 50px;
    width: 35%;
    margin: auto 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.encabezado .menu .menu-items{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 40%;
}

.menu-items li a {
    display: block;
    visibility: hidden;
    text-decoration: none;
    text-align: center;
    font-size: 15px;
    padding: 10px 15px;
    margin: 0 2px;
    color: #000;
}

.inactivo:hover{
    border-bottom: 1px solid rgb(55, 110, 182);
}

#activo{
    border-bottom: 1px solid rgb(55, 110, 182);
    color: rgb(55, 110, 182);
    cursor: default;
}

@media (max-width: 1180px){
    .encabezado {
        padding: 0 50px;
    }

    .encabezado .menu{
        width: 45%;
        padding: 0;
    }
}

@media (max-width: 951px){
    .contactos{
        padding: 10px 50px;
    }

    .encabezado{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 30px;
    }

    #titulo{
        z-index: 100;
    }

    .encabezado .menu {
        visibility: hidden;
        z-index: 10;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgb(14, 59, 95);
        opacity: 0.9;
        padding-top: 100px;
    }

    .encabezado .boton-menu{
        visibility: visible;
        margin: auto 0;
        margin-right: 55px;
        z-index: 100;
        font-size: 35px;
    }

    .encabezado .menu .menu-items{
        display: flex;
        visibility: hidden;
        flex-direction: column;
        align-items: center;
        height: 25%;
        width: 100%;
        padding: 25px 0;
        margin: 0 auto;
        margin-top: 15px;
    }

    .menu-items li{
        display: block;
        width: 80%;
    }

    .menu-items li a {
        text-align: center;
        color: #fff;
        border-bottom: 1px solid #fff;
        height: 20px;
        font-size: 25px;
    }

    #activo{
        border: none;
        border-radius: 0;
        color: #fff;
        border-bottom: 1px solid #fff;
    }
